import React, { useState, useEffect } from 'react';

const UserProfile = () => {
  const [user, setUser] = useState({
    name: '',
    email: '',
    phone: '',
    profileImage: '',
  });
  
  // Assuming getProfileInfo is a function that fetches the user data
  const getProfileInfo = async () => {
    try {
      // Fetch the profile data from your API or method
      const response = await fetch('/api/user/profile'); // Replace with your actual API endpoint
      const data = await response.json();
      setUser(data); // Update the state with the fetched data
    } catch (error) {
      console.error('Error fetching profile data:', error);
    }
  };

  useEffect(() => {
    getProfileInfo(); // Call the function when the component mounts
  }, []); // Empty dependency array to run once on mount

  return (
    <div className="max-w-4xl mx-auto p-6 bg-white rounded-lg shadow-md">
      <div className="flex items-center mb-6">
        <div className="w-16 h-16 rounded-full overflow-hidden bg-gray-200 flex items-center justify-center">
          {user.profileImage ? (
            <img src={user.profileImage} alt="Profile" className="w-full h-full object-cover" />
          ) : (
            <span className="text-3xl font-bold text-gray-500">{user.name.charAt(0)}</span>
          )}
        </div>
        <div className="ml-4">
          <h2 className="text-2xl font-semibold text-gray-800">{user.name}</h2>
          <p className="text-gray-600">{user.email}</p>
          <p className="text-gray-600">{user.phone}</p>
        </div>
      </div>

      <div className="grid grid-cols-2 gap-4">
        <div className="bg-gray-100 p-4 rounded-lg shadow">
          <h3 className="text-lg font-semibold text-gray-800">Orders</h3>
          <p className="text-gray-600">View your past orders</p>
          <button className="mt-2 text-blue-500 hover:underline">View Orders</button>
        </div>
        <div className="bg-gray-100 p-4 rounded-lg shadow">
          <h3 className="text-lg font-semibold text-gray-800">Wishlist</h3>
          <p className="text-gray-600">Check your saved items</p>
          <button className="mt-2 text-blue-500 hover:underline">View Wishlist</button>
        </div>
        <div className="bg-gray-100 p-4 rounded-lg shadow">
          <h3 className="text-lg font-semibold text-gray-800">Edit Profile</h3>
          <p className="text-gray-600">Update your information</p>
          <button className="mt-2 text-blue-500 hover:underline">Edit Profile</button>
        </div>
        <div className="bg-gray-100 p-4 rounded-lg shadow">
          <h3 className="text-lg font-semibold text-gray-800">Address</h3>
          <p className="text-gray-600">Manage your addresses</p>
          <button className="mt-2 text-blue-500 hover:underline">Manage Address</button>
        </div>
      </div>
    </div>
  );
};

export default UserProfile;
